<template>
  <div>
    <app-heart-loader v-if="this.isLoading"></app-heart-loader>
    <el-row v-else type="flex" justify="center">
      <el-col :xs="24" :sm="20" :md="18" :lg="16" :xl="12" type="flex" align="middle">
        <div v-if="userCart">
          <p id="cart_title" align="left">
            <img src="@/assets/icons/cart_bag.svg" id="cart_bag" alt="">
            
          </p>
          <p v-if="userCart.length === 0" class="cart_text">
              
          </p>
          <router-link to="/shop">
            <p
              v-if="userCart.length === 0"
              id="into_catalog">
               
              <v-icon class="secondary--text">arrow_forward</v-icon>
            </p>
          </router-link>
          <!--PRODUCTS-->
          <v-divider class="secondary mt-3 mb-3"></v-divider>
          <div
            v-for="product in userCart"
            :key="product.productId"
            v-if="product">
            <el-row
              type="flex"
              justify="center"
              style="flex-wrap: wrap; align-items: center">
              <el-col :sm="3" :md="3" :lg="3" :xl="3" align="left">
                <img
                  v-if="product.img_0.thumbnail"
                  :src="product.img_0.thumbnail"
                  ref="img_0" class="thumb_img"/>
              </el-col>
              <el-col :xs="24" :sm="8" :md="8" :lg="8" :xl="8" align="left" class="pr-2">
                <router-link :to="'/product/' + product.productId">
                  <p class="cart_text">
                    {{ product.title }},
                    {{ product.color }}
                  </p>
                  <span class="cart_sub_text">
                  : {{ product.SKU }}
                </span>
                </router-link>
              </el-col>
              <el-col :xs="24" :sm="4" :md="4" :lg="4" :xl="4" align="left">
                <el-input-number
                  size="small"
                  v-model="product.qty"
                  :min="1"
                  :max="product.totalQty">
                </el-input-number>
              </el-col>
              <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3" align="center">
                <p class="cart_text mb-0">
                  {{ parseFloat(product.qty * product.price).toFixed(2) }} &#8381;
                </p>
              </el-col>
              <el-col :xs="24" :sm="2" :md="2" :lg="2" :xl="2" align="center">
                <i
                  @click="removeFromCart(product.productId)"
                  class="el-icon-close remove_product secondary--text">
                </i>
              </el-col>
              <el-col :xs="24" :sm="3" :md="3" :lg="3" :xl="3" align="right">
                <checkout
                  type="single"
                  :checkout-obj="[{productId: product.productId, qty: product.qty}]">
                </checkout>
              </el-col>
            </el-row>
            <v-divider class="secondary mt-3 mb-3"></v-divider>
          </div>
          <div v-if="userCart.length > 1">
            <el-row type="flex">
              <el-col :span="9" align="left">
                <div class="main_btn continue_main_btn">
                   
                </div>
              </el-col>
              <el-col :span="9" align="left">
                <div class="paypal_total_btn">
                  <checkout
                    type="all"
                    :checkout-obj="totalItems">
                  </checkout>
                </div>
              </el-col>
              <el-col align="right">
                <p class="total_price">
                  : {{ parseFloat(totalPrice).toFixed(2) }} &#8381;
                </p>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-col>
    </el-row>
    <!--ORDERS HISTORY-->
    <orders-history class="mt-5"></orders-history>
  </div>
</template>

<script>
import Checkout from '@/components/shop/Checkout'
import OrdersHistory from '@/components/shop/OrdersHistory'

export default {
  name: 'ShoppingCart',
  components: {
    Checkout,
    OrdersHistory
  },
  data () {
    return {
      cartProduct: ''
    }
  },
  computed: {
    userCart () {
      let cart = this.$store.getters.cart
      let products = []
      let product
      if (cart.length > 0) {
        for (const productId of cart) {
          product = this.$store.getters.productById(productId)
          products.push(product)
        }
      }
      return products
    },
    totalPrice () {
      let total = 0
      let cart = this.userCart
      for (let product of cart) {
        if (product) {
          total += product.qty * product.price
        }
      }
      return total
    },
    totalItems () {
      let items = []
      let cart = this.userCart
      for (let el of cart) {
        if (el) {
          let item = {}
          item.productId = el.productId
          item.qty = el.qty
          items.push(item)
        }
      }
      return items
    }
  },
  methods: {
    removeFromCart (productId) {
      this.$store.dispatch('updateCart', {operation: 'remove', productId: productId})
    }
  }
}
</script>

<style scoped lang="scss">
  .thumb_img {
    height: 90px;
    width: 78px;
    object-fit: cover;
  }

  .cart_text {
    color: white;
  }

  .cart_sub_text {
    color: $color-info;
    font-size: 12px;
  }

  .total_price {
    color: white;
    font-size: 16px;
    font-weight: 600;
  }

  .remove_product {
    transform: scale(1.8);
  }

  .remove_product:hover {
    cursor: pointer;
  }

  #cart_title {
    color: $color-secondary;
    font-size: 18px;
  }

  #cart_bag {
    height: 28px;
    margin-bottom: -5px;
    margin-right: 2px;
  }

  #into_catalog {
    color: $color-secondary;
    font-size: 16px;
    font-weight: 600;
  }

  .continue_main_btn {
    font-size: 12px;
    width: 220px;
    height: 38px;
    margin-right: 40px;
  }
</style>



